body {
  margin: 40px;
  font-weight: bold;
}

ol, ul {
  margin: 10px;
  padding: 0;
  list-style-type: none;
}


.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.status {
  margin-bottom: 10px;
}

li > button {
color: teal;
border-radius: 16px;
background-color: transparent;
border: none;  
}

ol> li:first-child {

  margin-bottom: 10px;;
}
ol>li:first-child>button {
  padding: 12px;
  font-size: 20px;
  color: red;
  border: 1px solid teal;
  background-color: #f8e0e0;;
  background: linear-gradient(180deg, rgba(246,211,231,1) 0%, rgba(248,224,224,1) 35%, rgba(222,172,205,1) 100%, rgba(0,212,255,1) 100%);
  box-shadow: 4px 4px 6px 0px rgb(173, 231, 235);
  cursor: pointer;
  transition: all 0.3s;
}

ol>li:first-child>button:hover {
  transform: scale(1.05);
}

.square {
  background: #f8e0e0;
  border: 1px solid #bd5151;
  float: left;
  font-size: 28px;
  font-weight: bold;
  line-height: 34px;
  height: 64px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 64px;
  color: teal;
  box-shadow: 10px 10px 5px -1px rgba(200, 115, 115, 0.75);
  transition: all 0.3s;
}

.square:hover {
cursor:url(../public/cur.png),auto;
}

.square:focus {
  outline: none;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.game {
  display: flex;
  flex-direction: row;
  gap: 40px;
  
}

.game-info {
  width: 300px;
  color: rebeccapurple;
  text-align: center;
}

